.navigation-btn{
    border: 1px solid black;
    background: none;
    outline: none;
    padding: 5px 20px;
    color: black;
}

.move-list{
    padding: 10px 30px;
    border: 1px solid black;
    overflow-y: auto;
    height: 150px;
    border-radius: 5px;
}
.move-list-container{
    padding: 10px;
}
.move{
    display: inline-block;
    padding: 5px 20px;
    width: 50%;
    text-decoration: none;
    color: black;
    line-height: 1.5;
    border-radius: 5px;
}
.move:hover{
    /* background: #000080; */
    color: white;
    background:   hsl(87.2,35%,48.2%);;
}